import React, { useEffect, useState } from "react";
import {
  Col,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Input,
  ModalHeader,
  ModalBody,
  Modal,
  Row,
} from "reactstrap";
import Classes from "./allmedia.module.css";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";

const Library = (props) => {
  const [images, setImage] = useState([]);
  const [arrayHolder, setArrayHolder] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");

  const filterSearch = (event) => {
    setSearch(event.target.value);
    const value = arrayHolder.filter((item) => {
      const itemData = item.name.toLowerCase();
      const searchData = event.target.value.toLowerCase();
      return itemData.indexOf(searchData) > -1;
    });
    setImage(value);
  };

  const changeStatus = (id) => {
    const alldata = [...images];
    const newData = alldata.filter((item) => item._id === id);
    newData[0].checked = !newData[0].checked;
    setImage(alldata);
  };


  const { toggleSuccess, success, addImage } = props;

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
            query getAllMultimedia($page:Int,$limit:Int){
                getAllMultimedia(page :$page , limit :$limit){
                  _id,
                  name,
                  dir,
                  format,
                  dimWidth,
                  dimHeight,
                  createdAt
                }
              },
            `,
        variables: {
          page: 1,
          limit: 50,
        },
      },
    })
      .then((res) => {
        const { getAllMultimedia } = res.data.data;
        for (let index = 0; index < getAllMultimedia.length; index++) {
          const element = getAllMultimedia[index];
          element.checked = false;
        }
        setImage(getAllMultimedia);
        setArrayHolder(getAllMultimedia);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <div className="animated fadeIn">
      <Modal
        isOpen={success}
        toggle={toggleSuccess}
        className={"modal-lg " + props.className}
      >
        <ModalHeader toggle={toggleSuccess}>
          <Row>
            <Col md="12" xs="10">
              <InputGroup>
                <InputGroupAddon addonType="append">
                  <InputGroupText>جستجو تصویر</InputGroupText>
                </InputGroupAddon>
                <Input
                  placeholder="جستجو کنید....."
                  value={search}
                  onChange={filterSearch}
                />
              </InputGroup>
            </Col>
          </Row>
        </ModalHeader>
        <ModalBody>
          <div className={Classes.ModalAllMediaSection}>
            {loading ? (
              <Spinner animation="grow" size="sm" />
            ) : (
              images.map((item, index) => (
                <div className={Classes.ModalMediaSection} key={item._id}>
                  <label
                    htmlFor={`imageCheckBox${index}`}
                    onChange={() => {
                      addImage(item._id, item.dir);
                      changeStatus(item._id);
                    }}
                  >
                    <input
                      type="checkbox"
                      className={Classes.checkBox}
                      id={`imageCheckBox${index}`}
                    />

                    <img
                      src={`${process.env.REACT_APP_BASE_URL}${item.dir}`}
                      alt={item.dir}
                      name={`imageCheckBox${index}`}
                    />
                  </label>
                </div>
              ))
            )}
          </div>
        </ModalBody>
      </Modal>
    </div>
  );
};

export default Library;
