import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  CardFooter,
  Button,
  Input,
  FormGroup,
  Label,
  Table,
} from "reactstrap";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Library from "../Media/Library";
import Spinner from "reactstrap/lib/Spinner";

const Category = (props) => {
  const [success, setSuccess] = useState(false);
  const [newImage, setNewImage] = useState([]);
  const [values, setValues] = useState({
    title: "",
    label: "",
  });
  const [status, setStatus] = useState(false);
  const [Maincategory, setMaincategory] = useState("");
  const [Subcategory, setSubcategory] = useState("");
  const [allMainCategoryServerSide, setAllMainCategoryServerSide] = useState(
    []
  );
  const [allSubCategoryServerSide, setAllSubCategoryServerSide] = useState([]);
  const [CatId, setCatId] = useState(null);
  const [mainCategoryBoolean, setMainCategoryBoolean] = useState(false);
  const [subCategoryBoolean, setSubCategoryBoolean] = useState(false);
  const [result, setResult] = useState([]);

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name,
            label,
            parent{
              name
            }
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: false,
          hasParent: false,
          catId: null,
        },
      },
    })
      .then((res) => {
        //all category
        const { getAllCategory } = res.data.data;
        setResult(getAllCategory);

        //category parent null
        let arr = [];
        for (let index = 0; index < getAllCategory.length; index++) {
          const element = getAllCategory[index];
          if (element.parent == null) {
            arr.push(element);
          }
        }
        setAllMainCategoryServerSide(arr);
      })
      .catch((err) => console.log(err));
  }, []);



  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name,
            label,
            parent{
              name
            }
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: false,
          hasParent: true,
          catId: CatId,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setAllSubCategoryServerSide(getAllCategory);
      })
      .catch((err) => console.log(err));
  }, [status, mainCategoryBoolean, subCategoryBoolean, CatId]);

  const toggleSuccess = () => {
    if (newImage.length > 0) {
      toast.warn("اول تصویر پاک کن دوباره بزن");
      return false;
    }
    setSuccess(!success);
  };

  const addImage = (id, dir) => {
    const copyImage = [...newImage];
    copyImage.push({
      id: id,
      dir: dir,
    });
    setNewImage(copyImage);
    setSuccess(false);
  };

  const removeImage = (id) => {
    const copy = [...newImage];
    copy.splice(id, 1);
    setNewImage(copy);
  };

  const handleState = (e) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
  };

  const handleStateMain = (e) => {
    setMaincategory(e.target.value);
    setMainCategoryBoolean(true);
    setCatId(e.target.value);
    setStatus(!status);
  };

  const handleStateSub = (e) => {
    setSubcategory(e.target.value);
    setMainCategoryBoolean(false);
    setSubCategoryBoolean(true);
  };

  const handleSubmit = (e) => {
    if (values.title === "") {
      toast.error("فیلد نام نمی تواند خالی بماند");
      return false;
    }
    if (newImage.length === 0) {
      toast.error("عکس دسته بندی نباید خالی باشد");
      return false;
    }
    const senderImage = newImage[0].id;

    let parentID = null;
    if (mainCategoryBoolean === true && subCategoryBoolean === false) {
      parentID = Maincategory;
    } else if (mainCategoryBoolean === false && subCategoryBoolean === true) {
      parentID = Subcategory;
    }

    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        mutation addcategory($name:String!,$label:String,$parent:ID,$image:ID!) {
          category(input:{name:$name,label:$label,parent:$parent,image:$image}){
            status
          }
        }`,
        variables: {
          name: values.title,
          label: values.label,
          parent: parentID,
          image: senderImage,
        },
      },
    })
      .then((res) => {
        const { status } = res.data.data.category;
        if (status === 200) {
          toast.success("دسته بندی با موفقیت ایجاد شد");
        } else {
          toast.error(res.data.errors[0].data[0].message);
        }
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>دسته بندی محصولات</CardHeader>
            <CardBody>
              <FormGroup row>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="title">عنوان</Label>
                    <Input
                      type="text"
                      id="title"
                      placeholder="عنوان دسته بندی را وارد نمایید"
                      name="title"
                      value={values.title}
                      onChange={handleState}
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="label">توضیحات</Label>
                    <Input
                      type="text"
                      id="label"
                      placeholder="توضیحات دسته بندی را وارد نمایید"
                      value={values.label}
                      onChange={handleState}
                      name="label"
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="Maincategory">دسته اصلی</Label>
                    <Input
                      type="select"
                      name="Maincategory"
                      id="Maincategory"
                      value={Maincategory}
                      onChange={handleStateMain}
                    >
                      <option></option>
                      {allMainCategoryServerSide.map((item) => (
                        <option value={item._id} key={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="subMaincategory">دسته دوم</Label>
                    <Input
                      type="select"
                      name="Subcategory"
                      id="Subcategory"
                      value={Subcategory}
                      onChange={handleStateSub}
                    >
                      <option></option>
                      {allSubCategoryServerSide.map((item) => (
                        <option value={item._id} key={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col md="2" xs="8">
                  <Button block color="primary" onClick={toggleSuccess}>
                    انتخاب تصویر
                  </Button>
                </Col>
                <Col md="2" xs="8">
                  {newImage.map((item, index) => (
                    <div
                      style={{ position: "relative", width: "200px" }}
                      key={index}
                      className="my-2"
                    >
                      <span
                        style={{
                          position: "absolute",
                          top: "-24px",
                          cursor: "pointer",
                          color: "red",
                        }}
                        onClick={() => removeImage(item._id)}
                      >
                        <i className="fa fa-remove fa-lg mt-4"></i>
                      </span>
                      <img
                        src={`${process.env.REACT_APP_BASE_URL}${item.dir}`}
                        alt={item.dir}
                        style={{ width: "100%" }}
                      />
                    </div>
                  ))}
                </Col>
              </FormGroup>
            </CardBody>
            <CardFooter>
              <Button
                type="submit"
                color="primary"
                size="sm"
                onClick={handleSubmit}
              >
                آپلود
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>
      {success ? (
        <Library
          success={success}
          toggleSuccess={toggleSuccess}
          addImage={addImage}
        />
      ) : null}

      <Row>
        <Col>
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i>همه دسته بندی ها
            </CardHeader>
            <CardBody>
              <Table hover bordered striped responsive size="sm">
                <thead>
                  <tr>
                    <th>نام دسته بندی</th>
                    <th>نام والد</th>
                  </tr>
                </thead>
                <tbody>
                  {result.length > 0 ? (
                    result.map((val) => (
                      <tr>
                        <td>{val.name}</td>
                        <td>{val.parent !== null ?  val.parent.name : 'دسته اصلی'}</td>
                      </tr>
                    ))
                  ) : (
                    <center>
                      <Spinner size="sm" />
                    </center>
                  )}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Category;
